<template>
    <div v-loading="loading">
        <el-form ref="form" :model="form" label-width="110px">
            <el-row class="toolbar bgc-fff">
                <el-col :span="12">
                    <h2 class="fs-18">当前订单状态：<span  class="color-red">{{form.work_status_name}}({{form.stage_name}})</span></h2>
                    <p class="fs-14">当前处理人：{{form.operater}}</p>
                </el-col>
                <!-- <el-col :span="12" class="ta-r">
                    <el-button type="primary" icon="plus">导出</el-button>
                    <el-button type="primary" icon="plus">打印</el-button>
                </el-col> -->
            </el-row>
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="vin号">
                                {{form.basic_info.vin_number}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="车主姓名">
                                {{form.basic_info.band_name}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="车辆品牌">
                                {{form.basic_info.car_brand}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="移动电话">
                                {{form.basic_info.mobile}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="公告型号">
                                {{form.basic_info.foreign_model}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="挂靠公司">
                                {{form.basic_info.bus_mech_name}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="马力">
                                {{form.basic_info.horse_power}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="是否办理信贷">
                                <el-radio-group v-model="form.is_credit" disabled>
                                    <el-radio label="2">办理</el-radio>
                                    <el-radio label="1">不办理</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row>
                        <el-col :span="12">
                            <el-form-item label="驱动形式">
                                {{form.basic_info.drive_form}}
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label="颜色">
                                {{form.basic_info.color}}
                            </el-form-item>
                        </el-col>
                    </el-row>

                    <el-row v-if="form.stage_id==8">
                        <el-col :span="12">
                            <el-form-item label="是否先结算">
                                <el-radio-group v-model="form.pay_info.is_settle" :disabled="form.work_status!=1 && form.work_status!=3">
                                    <el-radio label="1">先结算</el-radio>
                                    <el-radio label="2">后结算</el-radio>
                                </el-radio-group>
                            </el-form-item>
                        </el-col>
                    </el-row>

                </el-collapse-item>

                <el-collapse-item title="　保险信息" name="2">
                    
                    <el-table :data="form.some_insurances" border>
                        <el-table-column prop="policy_name" label="保险名称" width="150">
                        </el-table-column>
                        <el-table-column prop="policy_type" label="保险种类" width="120">
                        </el-table-column>
                        <el-table-column prop="policy_company" label="保险公司">
                        </el-table-column>
                        <el-table-column prop="policy_code" label="保单号">
                        </el-table-column>
                        <el-table-column prop="policy_money" label="金额">
                        </el-table-column>
                        <el-table-column label="操作" width="180" align="center" fixed="right">
                            <template slot-scope="{row, $index}">
                                <el-button type="text" icon="el-icon-delete" @click="handleDelete(row, $index)" v-if="isDisabledDel()">删除</el-button>
                                <el-button type="text" icon="el-icon-setting" @click="handleSetting(row, $index)" v-if="isDisabledSet()">设置</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <div class="toolbar ta-r mt10">
                        <el-button class="mr10" @click="dialogHandleAddInsurance" v-if="form.stage_id==14 || form.stage_id==0">选择保险方案</el-button>
                        <span class="mr10">共选择<b class="color-red fs-16"> {{form.some_insurances.length}} </b>件商品</span>
                        <el-tag type="danger">￥：{{grossAmount}}</el-tag>
                    </div>
                </el-collapse-item>

                <el-collapse-item title="　收款信息" name="3">

                    <el-form-item label="收款信息选择">
                        <el-radio-group v-model="form.some_collect_cast.pay_type" @change="getExecUser" :disabled="isDisabledReceipt()">
                            <el-radio label="1">立即支付</el-radio>
                            <el-radio label="2">延迟付款</el-radio>
                            <el-radio label="3">已支付</el-radio>
                        </el-radio-group>
                    </el-form-item>

                    <el-form-item label="费用金额" v-if="form.some_collect_cast.pay_type == 2" :disabled="isDisabledReceipt()">
                        <el-input v-model="form.some_collect_cast.money"></el-input>
                    </el-form-item>

                    <el-row v-if="form.some_collect_cast.pay_type == 1">
                        
                        <el-form-item label="车主支付方式">
                            <el-radio-group v-model="form.some_collect_cast.pay_method" @change="getExecUser" :disabled="isDisabledReceipt()">
                                <el-radio label="1">现付</el-radio>
                                <el-radio label="2">转账</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="本次费用">
                            <el-input v-model="form.some_collect_cast.this_cost" @change="handelOutstanding" :disabled="isDisabledReceipt()"></el-input>
                        </el-form-item>

                        <el-form-item label="未结清费用">
                            <el-checkbox-group v-model="form.some_collect_cast.choose" @change="handelOutstanding" :disabled="isDisabledReceipt()">
                                <el-checkbox v-for="item in form.no_pay" :label="item.id" :key="item.id">{{item.item_name}} ￥{{item.money}}</el-checkbox>
                            </el-checkbox-group>
                        </el-form-item>

                        <el-form-item label="费用凭证">
                            <el-upload
                                :disabled="isDisabledReceipt()"
                                class="avatar-uploader"
                                :action="IMG_UPLOAD_URL"
                                :show-file-list="false"
                                :on-success="handleUploadSuccess_cost_img"
                                :before-upload="handleBeforeUpload">
                                <img v-if="form.some_collect_cast.cost_img" :src="form.some_collect_cast.cost_img" class="avatar">
                                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                        </el-form-item>

                        <el-form-item label="支付总费用">
                            {{form.some_collect_cast.together_cost}}
                        </el-form-item>

                    </el-row>

                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.some_collect_cast.refund_remark" :disabled="isDisabledReceipt()"></el-input>
                    </el-form-item>
            
                    <el-form-item label="选择审核人" class="mt15" v-if="isShowFirstAuditor()">
                        <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                            <el-option
                                v-for="item in optionsCompany"
                                :key="item.id"
                                :label="item.mc_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                        <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                            <el-option
                                v-for="item in optionsExecUser"
                                :key="item.id"
                                :label="item.user_name"
                                :value="item.id">
                            </el-option>
                        </el-select>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　付款信息" name="4" v-if="isShowPayment()">

                    <el-form-item label="付款金额">
                        <el-input v-model="form.pay_info.money" :disabled="isDisabledPayment()"></el-input>
                    </el-form-item>

                    <el-form-item label="付款银行">
                        <el-input v-model="form.pay_info.pay_bank" :disabled="isDisabledPayment()"></el-input>
                    </el-form-item>

                    <el-form-item label="付款银行账户">
                        <el-input v-model="form.pay_info.pay_bank_account" :disabled="isDisabledPayment()"></el-input>
                    </el-form-item>

                    <el-form-item label="备注">
                        <el-input type="textarea" v-model="form.pay_info.pay_remark" :disabled="isDisabledPayment()"></el-input>
                    </el-form-item>

                    <el-form-item label="出纳打款凭证"v-if="isShowVoucher()">
                        <el-upload
                            :disabled="isDisabledVoucher()"
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_pay_img"
                            :before-upload="handleBeforeUpload">
                            <img v-if="form.pay_info.pay_img" :src="form.pay_info.pay_img" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    
                    <el-form-item label="备注" v-if="isShowVoucher()">
                        <el-input type="textarea" v-model="form.pay_info.pay_img_remark" :disabled="isDisabledVoucher()"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　审核信息" name="5" v-if="isShowAudit()">

                    <el-collapse v-model="collapseAuditActive">

                        <el-collapse-item title="　保险费用收款阶段" name="1">
                            <el-card class="box-card mb15" v-for="(item,index) in form.handle_record.refund" :key="index">
                                <div><span class="display-ib ta-r w70">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w70">审核意见：</span>{{item.exec_op_type==1?'通过':'驳回'}}</div>
                                <div><span class="display-ib ta-r w70">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w70">审核人ID：</span>{{item.exec_user_name}}</div>
                                <div><span class="display-ib ta-r w70">审核时间：</span>{{item.create_time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <el-collapse-item title="　保险费用付款阶段" name="2" v-if="isShowPayment()">
                            <el-card class="box-card mb15" v-for="(item,index) in form.handle_record.refund" :key="index">
                                <div><span class="display-ib ta-r w70">订单流程：</span>{{item.node_name}}</div>
                                <div><span class="display-ib ta-r w70">审核意见：</span>{{item.exec_op_type==1?'通过':'驳回'}}</div>
                                <div><span class="display-ib ta-r w70">备注：</span>{{item.exec_content}}</div>
                                <div><span class="display-ib ta-r w70">审核人ID：</span>{{item.exec_user_name}}</div>
                                <div><span class="display-ib ta-r w70">审核时间：</span>{{item.create_time}}</div>
                            </el-card>
                        </el-collapse-item>

                        <template v-if="isShowOpinion()">
                            <el-form-item label="审核意见" class="mt15">
                                <el-select v-model="form.opinion" placeholder="请选择" class="mr10">
                                    <el-option label="通过" value="1"></el-option>
                                    <el-option label="驳回" value="2"></el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="备注">
                                <el-input type="textarea" v-model="form.remark"></el-input>
                            </el-form-item>
                            <el-form-item label="选择审核人" class="mt15" v-if="form.opinion==1">
                                <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                    <el-option
                                        v-for="item in optionsCompany"
                                        :key="item.id"
                                        :label="item.mc_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                                    <el-option
                                        v-for="item in optionsExecUser"
                                        :key="item.id"
                                        :label="item.user_name"
                                        :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </template>

                        <el-form-item label="选择审核人" class="mt15" v-if="isShowLastAudit()">
                            <el-select v-model="form.cp_id" placeholder="请选择公司" class="mr10" @change="handleSelectAuditor">
                                <el-option
                                    v-for="item in optionsCompany"
                                    :key="item.id"
                                    :label="item.mc_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                            <el-select v-model="form.after_user_id" placeholder="请选择审核人">
                                <el-option
                                    v-for="item in optionsExecUser"
                                    :key="item.id"
                                    :label="item.user_name"
                                    :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>

                    </el-collapse>
                </el-collapse-item>
            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit(1)" v-if="pageType != 'view'">提 交</el-button>
                <el-button type="primary" @click="handleSubmit(2)" v-if="isShowSave()">保 存</el-button>
                <el-button @click="$router.back()">返 回</el-button>
            </el-form-item>
        </el-form>
    

        <!-- 添加保险 -->
        <el-dialog title="添加保险" :visible.sync="dialogAddInsurance" width="50%" @close="dialogCloseCallback">
            <el-form ref="filtersAddInsurance" :inline="true" :model="filtersAddInsurance">
                    <el-form-item label="选择投保公司" :inline="true">
                        <el-select v-model="filtersAddInsurance.policy_company_id" placeholder="请选择" @change="handleChangeInsurance">
                            <el-option
                                v-for="item in policyOptions"
                                :key="item.policy_company_id"
                                :label="item.policy_company"
                                :value="item.policy_company_id">
                            </el-option>
                        </el-select>
                    </el-form-item>
            </el-form>
            <el-table :data="dataAddInsurance" border ref="multipleTable" @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55" align="center">
                </el-table-column>
                <el-table-column prop="policy_name" label="保险名称" width="150">
                </el-table-column>
                <el-table-column prop="policy_type" label="保险类型" width="120">
                </el-table-column>
                <el-table-column prop="policy_money" label="价格">
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogAddInsurance = false">取 消</el-button>
                <el-button type="primary" @click="dialogSubmitAddInsurance">添 加</el-button>
            </span>
        </el-dialog>

        <!-- 保单 -->
        <el-dialog title="保单" :visible.sync="dialogGuarantee" width="50%">
            <el-form ref="formGuarantee" :model="formGuarantee" label-width="80px">
                    <el-form-item label="保险名称">
                        {{formGuarantee.policy_name}}
                    </el-form-item>
                    <el-form-item label="保险种类">
                        {{formGuarantee.policy_type}}
                    </el-form-item>
                    <el-form-item label="保险公司">
                        {{formGuarantee.policy_company}}
                    </el-form-item>
                    <el-form-item label="保单号">
                        <el-input v-model="formGuarantee.policy_code"></el-input>
                    </el-form-item>
                    <el-form-item label="保单照片">
                        <el-upload
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_pic_url"
                            :before-upload="handleBeforeUpload">
                            <img v-if="formGuarantee.pic_url" :src="formGuarantee.pic_url" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
                    <el-form-item label="保单发票">
                        <el-upload
                            class="avatar-uploader"
                            :action="IMG_UPLOAD_URL"
                            :show-file-list="false"
                            :on-success="handleUploadSuccess_pic_invoice"
                            :before-upload="handleBeforeUpload">
                            <img v-if="formGuarantee.pic_invoice" :src="formGuarantee.pic_invoice" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                        </el-upload>
                    </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogGuarantee = false">返 回</el-button>
                <el-button type="primary" @click="dialogSubmitGuarantee">确 认</el-button>
            </span>
        </el-dialog>
        
    </div>
</template>

<script>
    
    import * as API from '@/api/trafficManagement'
    import { organizationListUl } from '@/api/enterpriseManagement'
    import { requestUrl } from '@/api/config'
    export default {
        data: function(){
            return {
                IMG_UPLOAD_URL: `${requestUrl}/home/UpLoadFile/uploadImg`,
                collapseActive: ['1'],
                collapseAuditActive: ['1'],
                pageType: '',
                dialogAddInsurance: false,
                dialogGuarantee: false,
                loading: true,
                activeGuarantee: '',
                filtersAddInsurance: {
                    brand:'',
                    series:'',
                    type:'',
                    drive:''
                },
                formGuarantee: {
                    policy_code: '',
                    pic_url: '',
                    pic_invoice: '',
                },
                policy_company_id:'',
                policyOptions:[],
                multipleSelection: [],
                // grossAmount:0,
                form: {
                    after_user_id: '',
                    cp_id: '',
                    id: "1",
                    bus_id: "1",
                    is_credit: "1",
                    stage_id: "1", // 1收款阶段， 2 付款阶段
                    work_id: "0",
                    node_id: "0",
                    status: "1",
                    opinion: "1",
                    no_pay: [],
                    handle_record:{
                        refund:[],
                        pay:[]
                    },
                    basic_info: {
                        vin_number: "111",
                        bus_id: "1",
                        foreign_model: "",
                        fuel_type: '',
                        horse_power: '',
                        drive_form: '',
                        color: "",
                        per_owner_id: "1",
                        bus_mech_id: "0",
                        mechanism_name: '',
                        bus_user_name: "张三",
                        mobile: "11111111"
                    },
                    some_insurances: [],
                    some_collect_cast: {
                        money: '88888',
                        choose: [],
                        pay_type: '1',
                        pay_method: '1',
                        this_cost: '88888',
                        non_payment: '88888',
                        together_cost: '88888',
                        cost_img: 'http://zhonghuan-1257386775.cos.ap-beijing.myqcloud.com/development%2Fimage%2Fb2c06d360e139ebe0068a9b8bbe3f2bc.jpg'
                    },
                    pay_info: {
                        money: '',
                        pay_bank: '',
                        pay_bank_account: '',
                        pay_remark: '',
                        pay_img: '',
                        is_settle: "1",
                        pay_img_remark: ''
                    },
                },
                dataAddInsurance:[],
                optionsExecUser:[],
                optionsCompany:[],
            }
        },
        created(){
            this.checkPath();
        },
        computed:{
            grossAmount(){
                let zong = 0;
                this.form.some_insurances.forEach((item, index)=>{
                    zong += Number(item.policy_money)
                })
                return zong;
            }
        },
        watch:{
            
        },
        methods: {
            // 是否禁止输入
            isDisabled () {
                let d = this.pageType == 'view' ? true : false;
                return d;
            },
            isDisabledSet(){
                
                if(this.form.pay_info.is_settle==1 && this.form.node_id == 67){
                    return true
                }else if(this.form.pay_info.is_settle==2 && this.form.node_id == 73){
                    return true
                }
            },
            isDisabledDel () {
                if(this.form.stage_id==0){
                    return true
                }
                if(this.form.stage_id==1){
                    return this.form.stage_id==12 && this.form.stage_id==13
                }
            },
            isDisabledReceipt() {
                if(this.form.stage_id==7 || this.form.stage_id==0){
                    return this.form.work_status!=1 && this.form.work_status!=3
                }else{
                    return true
                }
            },
            isDisabledVoucher(){
                return this.form.node_id > 72
            },
            isDisabledPayment() {
                return this.form.work_status==2
            },
            isShowPayment(){
                return this.form.node_id>=67
            },
            isShowVoucher(){
                return this.form.node_id >= 72
            },
            isShowLastAudit(){
                if(this.form.stage_id==8){
                    return this.form.work_status==1 || this.form.work_status==3
                }
            },
            isShowOpinion(){
                return this.form.work_status==2 && this.form.node_id != 73
            },
            isShowSave(){
                if(this.pageType != 'view'){
                    return this.form.work_status!=2
                }
            },
            isShowAudit(){
                if(this.form.stage_id==0 || this.form.stage_id==7){
                    return this.form.work_status!=1
                }else{
                    return true
                }
            },
            isShowFirstAuditor(){
                return this.form.node_id==55 || this.form.node_id==62  || this.form.node_id==0
            },
            checkPath() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                    _path.indexOf('view') > -1 ? this.pageType='view' : 'edit'

                // 公司列表
                organizationListUl({}).then(res => {
                    if(res.code == 0){
                        this.optionsCompany = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })

                API.apiInsuranceView({id:_query.id}).then(res => {
                    this.loading = false
                    if(res.code == 0){
                        Object.assign(this.form, res.data);
                        if(this.pageType!='view'){
                            this.getExecUser()
                        }
                    }else{
                        this.$message.error(res.msg)
                    }
                })


            },
            getExecUser(){
                // 审核人
                let type='',
                    get_type=''
                if(this.form.some_collect_cast.pay_type == 1){
                    this.form.some_collect_cast.pay_method==1?type=1:type=2
                }else{
                    type=3
                }
                if(this.form.stage_id==7 || this.form.stage_id==0){
                    if(this.form.work_status==1 || this.form.work_status==3){
                        get_type=1
                    }else{
                        get_type=2
                    }
                }else{
                    if(this.form.work_status==1 || this.form.work_status==3){
                        get_type=3
                    }else{
                        get_type=2
                    }
                }
                let params = {
                    cp_id:this.form.cp_id,
                    type:type,
                    get_type:get_type,
                    node_id:this.form.node_id,
                }
                this.form.after_user_id = ''
                API.apiInsuranceGetExecUser(params).then(res => {
                    if(res.code == 0){
                        this.optionsExecUser = res.data
                    }else{
                        this.$message.error(res.msg)
                    }
                })
            },
            handleSubmit(submitType) {
                if(this.form.stage_id==0 || this.form.node_id==55){
                    let policyId = []
                    this.form.some_insurances.map((item)=>{
                        policyId.push(item.policy_product_id)
                    })
                    let params = {
                        some_insurances: policyId,
                        is_credit: this.form.is_credit,
                        bus_id: this.form.bus_id,
                        id: this.form.id,
                        ...this.form.some_collect_cast,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                    }
                    API.apiInsuranceSaveRefundInfo(params).then(res => {
                        if(res.code == 0){
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }

                if(this.form.stage_id==8 && this.form.node_id==67){
                    
                    let params = {
                        ...this.form.pay_info,
                        bus_id: this.form.bus_id,
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                    }
                    API.apiInsuranceSavePayInfo(params).then(res => {
                        if(res.code == 0){
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
                if(this.form.work_status==2 && this.form.node_id!=67){
                    let payParams={
                        pay_img: this.form.pay_info.pay_img,
                        pay_id: this.form.pay_info.id,
                        id: this.form.pay_info.item_id,
                        pay_img_remark: this.form.pay_info.pay_img_remark,
                        perfect_info: 1
                    }
                    let params = {
                        id: this.form.id,
                        submit_type: submitType,
                        after_user_id: this.form.after_user_id,
                        remark: this.form.remark,
                        reject_type: this.form.stage_id==7?'1':'2',
                        exam_type: this.form.opinion,
                    }
                    if(this.form.node_id==72){
                        Object.assign(params, payParams)
                    }
                    API.apiInsuranceExecWork(params).then(res => {
                        if(res.code == 0){
                            this.$router.back()
                        }else{
                            this.$message.error(res.msg)
                        }
                    })
                }
            },
            handelOutstanding(){
                let num = 0
                if(this.form.no_pay.length<=0){
                    this.form.some_collect_cast.together_cost = this.form.some_collect_cast.this_cost
                }else{
                    this.form.no_pay_money.forEach(item2=>{
                        this.form.no_pay.forEach(item=>{
                            if(item2.id==item){
                                num+=Number(item2.money)
                            }
                        })
                    })
                    this.form.some_collect_cast.together_cost = num+Number(this.form.some_collect_cast.this_cost)
                }
            },
            handleUploadSuccess_cost_img(res, file, fileList){
                this.form.some_collect_cast.cost_img = res.data.url
            },
            handleUploadSuccess_pay_img(res, file, fileList){
                this.form.pay_info.pay_img = res.data.url
            },
            handleUploadSuccess_pic_url(res, file, fileList){
                this.formGuarantee.pic_url = res.data.url
            },
            handleUploadSuccess_pic_invoice(res, file, fileList){
                this.formGuarantee.pic_invoice = res.data.url
            },
            handleBeforeUpload(){

            },
            handleSelectAuditor(val){
                this.getExecUser();
            },
            handleDelete(row, index){
                this.form.some_insurances.splice(index, 1);
            },
            handleSetting(row, index){
                this.dialogGuarantee = true
                this.activeGuarantee = index
                Object.assign(this.formGuarantee,row)
                this.$forceUpdate()
            },
            handleChangeInsurance(value){
                API.apiInsuranceSelect({policy_company_id: value}).then(res => {
                    this.dataAddInsurance = res.data;
                })
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            dialogCloseCallback(){
                this.filtersAddInsurance.policy_company_id = ''
                this.dataAddInsurance = []
            },
            dialogHandleAddInsurance() {
                API.apiInsuranceCompanyList({}).then(res => {
                    this.policyOptions = res.data;
                    this.dialogAddInsurance = true;
                })
            },
            dialogSubmitAddInsurance(){
                this.multipleSelection.forEach((item, index)=>{
                    this.form.some_insurances.push(item)
                })
                this.dialogAddInsurance = false;
            },
            dialogSubmitGuarantee(){
                Object.assign(this.form.some_insurances[this.activeGuarantee],this.formGuarantee)
                API.apiInsurancePaymentPerfectInsInfo(this.formGuarantee).then(res => {
                    if(res.code ==0){
                        this.dialogGuarantee = false
                    }else{
                        this.$message.error(res.msg)
                    }
                    
                })
            }
        }
    }
</script>

